.race-track {
  text-align: center;
  padding: 20px;
}

.race-table {
  width: 80vh; /* Ancho de la tabla */
  margin: 0 auto;
  table-layout: fixed;
  border-collapse: collapse;
}

.race-table td {
  border: 1px solid #ccc;
  padding: 5px;
}

.horse-name {
  width: 5%;
}

.race-lane {
  width: 95%;
  position: relative;
  overflow: hidden;
}

.horse-dot {
  width: 20px; /* Tamaño del punto del caballo */
  height: 20px;
  background-color: #007bff;
  border-radius: 50%;
  position: absolute;
  bottom: 0;
}

.winner {
  background-color: #28a745; /* Color del ganador */
}

/* ... (estilos previos) */

/* Otras reglas de estilo */

@keyframes run-1-1 {
  0% {
    left: 0;
  }
  100% {
    left: calc(50% - 20px);
  }
}

@keyframes run-1-2 {
  0% {
    left: 0;
  }
  100% {
    left: calc(50% - 20px);
  }
}

@keyframes run-1-3 {
  0% {
    left: 0;
  }
  100% {
    left: calc(50% - 20px);
  }
}

@keyframes run-2-1 {
  0% {
    left: 50%;
  }
  100% {
    left: calc(100% - 20px);
  }
}

@keyframes run-2-2 {
  0% {
    left: 50%;
  }
  100% {
    left: calc(100% - 20px);
  }
}

@keyframes run-2-3 {
  0% {
    left: 50%;
  }
  100% {
    left: calc(100% - 20px);
  }
}
/* Animación para la primera etapa */
@keyframes run-1 {
  0% {
    left: 0;
  }
  100% {
    left: 50%; /* Posición final a la mitad */
  }
}

/* Animación para la segunda etapa */
@keyframes run-2 {
  0% {
    left: 50%; /* Posición inicial a la mitad */
  }
  100% {
    left: 100%; /* Posición final en la meta */
  }
}

/* ... (estilos posteriores) */


@keyframes hide {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

button {
  margin-top: 20px;
}
