/* styles.css */

.carreras-container {
  text-align: center;
  padding-top:2% ;
}

.carreras-container h1 {
  color: #167CBB;
  font-size: 1.5em;
}

.carreras-container button {
  width: 5vw; /* Ajusta el ancho del botón a 200 píxeles */
  height: 5vh;
  background-color:#14a73e98;
  color: white;
  padding: 1vp;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  cursor: pointer;
}

.carreras-container button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.carreras-container button.siguiente {
  background-color: #14a73e98;
}

.carreras-container ul {
  list-style-type: none;
  padding: 5%;
  
   margin-bottom: 0;
}

.carreras-container li {
  background-color: #767676;
  padding: 1vp;
  margin-bottom: 5px;
  border-radius: 5px;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.carreras-container li span {
  font-weight: bold;
}

.carreras-container li span .raceid {
  flex: 1;
}

.carreras-container li .name {
  flex: 3;
  text-align: center;
  color: #222;
  font-size: 1em;
}

.carreras-container li span {
  flex: 3;
  text-align: center;
  color: #222;
  font-size: 1em;
}
.carreras-container li .status {
  flex: 1;
}



/*.carreras-container li span {
  font-weight: bold;
}
*/
.carreras-container li:hover {
  background-color: #167CBB;
  color:#423F3F;

}

.carreras-container li .name:hover {
color: #423F3F;
}

@media screen and (max-width: 768px) {
  .carreras-container h1 {
    font-size: 1.2em;
  }
  
  .carreras-container li {
    flex-direction: column;
    text-align: center;
    width: 90%;
  }
}


.popup-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.popup-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
}

.popup-content h2 {
  margin-top: 0;
}

.popup-content p {
  margin-bottom: 1em;
}