.modal-race-container {
  position: absolute;
  min-width: 50vw;
  max-width: 70vw;
  min-height: 50vh;
  max-height: 70vh;
  border-radius: 20%;
  overflow-y: auto;
}

.data-container{
  justify-content: center;
}


.modal-race-close {
  z-index: 777;
  position: absolute;
  top: 1%;
  right: 2%;
  text-decoration-color: white;
background-color: red;
  padding: 0.5%;
}

.modal.is-open {
  display: flex;
}


.modal-race-container .card-race-info {
  position: relative;
  width: 100%;
  height: 100%;
  background: #2e2e2e;
  border-radius: 50%;
  display: flex;

}




.card-race-info .content-race {
  position: relative;
  min-width: 50vw;
  max-width: 50vw;
  min-height: 70%;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
    border-radius: 30%;
  text-align: center;
  background: #2e2e2e; 
  overflow-y: scroll;
  overflow-x: hidden;
  z-index: 10;

}



 


 





