/* Rareza, Minteo,Nombre,Name */
.card-race-info .rareza-race {
  position: absolute;
  z-index: 15;
  top: 5%;
  right: 3%;
  object-fit: cover;

  border-radius: 50%;
  display: inline-flex;
  transform: scaleX(-1);


  box-sizing: border-box;
  width: 19%;
  justify-content: center;
}

.card-race-info .rareza-race h2 {
  writing-mode: vertical-lr; /* o vertical-lr */
  text-shadow: 1px 1px 0 #ffd700;
  font-weight: bold;
}


.card-race-info .minteo-race {
position: relative;
z-index: 15;
bottom: 1em;
left: 75%;
object-fit: cover;
background: radial-gradient(circle, #F5F5F5, #F8F8FF);
border-radius: 50%;
border: 3px solid #000000;

box-sizing: border-box;
width: 12%;
justify-content: center;
}

.card-race-info .minteo-race p {
  position: relative;
  color: black;
  font-size: 0.7em;
   font-weight: bold;
}

.card-race-info .minteo-race h2 {
  position: relative;
  color: black;
  font-size: 1.5em;
   font-weight: bold;
}

.card-race-info .content-race p.Status {
  background: #2e2e2e;
   font-weight: bold;
  border-radius: 5em;
  font-size: 1.3em;
  font-style: italic;
  display: inline; /* Cambiado de inline-block a inline */
  position: relative;
  z-index: 10;
  left: 2%;
  top: 0.5em;
  text-shadow: 
    1px -1px 0 #767676, 
    -1px 2px 1px #737272, 
    -2px 4px 1px #767474, 
    -3px 6px 1px #787777, 
    -4px 8px 1px #7b7a7a, 
    -5px 10px 1px #7f7d7d;
  font-style: italic;
  font-weight: 1px;
  text-transform: initial;
  color: darkred;
}

.card-race-info .name{
  display: flex;

  align-items: center;
  left: 5em;
  top: -3em;
position: relative;
object-fit: cover;
}





/* Estilos para las imágenes */
.img-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  width: 80%;
}

.img-container img {
  max-width: 3rem;
  height: auto;
}



.card-race-info .content-race .icon {
  color: var(--color);
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  background: #2e2e2e;
  box-shadow: 0 0 0 4px #2e2e2e,
    0 0 0 6px var(--color);
  transition: 0.5s ease-in-out;

}

.card-race-info:hover .content-race .icon {
  background: var(--color);
  color: #2e2e2e;
  box-shadow: 0 0 0 4px #2e2e2e,
    0 0 0 300px var(--color);
}






.card-race-info-c {
  position: relative;
  width: 100%;
  height: 100%;
  background: #ffff;
  display: flex;

}

.card-race-info-c .content-race {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 1px 1px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #854D0D;
  overflow: hidden;
  z-index: 10;
}



.actions-race {
  position: relative;
  padding: 10px;
  padding-left: 10px;
  padding-right: 10px;
  width: 100%;
  height: 100%;
  background: #2e2e2e;
  display: flex;
  justify-content: space-around;

}
.actions-race input{
  
 justify-self: center; 
}









