@keyframes neonText {
  from {
    background-position: 100%;
  }
  to {
    background-position: -100%;
  }
}

h1 {
  font-size: 2em;
  padding: 1em;
  border: 1px solid blue;
  background: linear-gradient(90deg, transparent, #00f, transparent);
  background-size: 200% 100%;
  color: #00f;
  text-align: center;
  animation: neonText 2s linear infinite;
}

