  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 10%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.75);
  display: none;
 /* justify-content: center;
  align-items: center;
*/}


.modal.container.pantallantigua {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: black;
  color: yellow;
  font-size: 10px;
  padding: 5vw;
  border-radius: 10px;
  z-index: 25;
}


table {
  border-collapse: collapse;
  background-color: black;
  color: yellow;
  font-family: monospace;
}

td, th {
  border: 1px solid yellow;
  padding: 0.5em;
}

span{
  color: gold;
 font-size: 0.5rem;
top: -1em;
}



.modal-container {
  position: relative;
  padding: 1rem;
  min-width: 40vw;
  max-width: 60vw;
  min-height: 60vh;
  max-height: 80vh;
  overflow-y: scroll;
  background-color:#55686f;
}

.modal-close {
  z-index: 26;
  position: absolute;
  top: 1rem;
  right: 1rem;
  text-decoration-color: white;
}

.modal.is-open {
  display: flex;
}



.container-info {
  position: relative;
  width: 100%;
  min-height: 50vh;
  display: flex;
/*  justify-content: center;
  align-items: center;*/
  flex-wrap: wrap;


  background: #222;
}

.card-info {
  position: relative;
  width: 100%;
  height: 100%;
  background: #2e2e2e;
  display: flex;

}



.card-info .content {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  text-align: center;
  background: #2e2e2e;
  gap: 20px;
  overflow: hidden;
  z-index: 10;
}


.card-info .content-img {
  display: flex;
  position: relative;
  max-width: 30vw;
  max-height: 30vh;
  border-radius: 30%;
  top: 10px ;
  overflow: hidden;
  z-index: 10;
}


.card-info .content .salud{
z-index:15;
width: 10vw;
position: absolute;
top: 2px;
right: 3%;
text-align:left;
object-fit: cover;
display: flex;
}

.card-info .content .salud svg{
width: 1.2rem;
height: 1.2rem;
fill:white;
}

.card-info .content .salud span {
display: none;
}

.card-info .content .salud:hover span {
 background: rgb(255, 0, 0,0.1);
 color: black;
 font-size: 1em;
  display: block;
}



.barra-container {
  width: 100%; /* Ancho completo para ocupar el espacio disponible */
  height: 3.2vh;
  background-color: #222;
  border-radius: 5px ;
  border: 1px solid grey;
  overflow: hidden;
  margin-top: 0.5rem;
  margin-right: 0.8rem;
}

.barra {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  transition: width 0.3s ease-in-out; /* Agregar transición suave para el cambio de ancho */
}

.barra p{
color: black;}

.barra-label {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 3vw;
  color: white;
  font-size: 0.8em;

}





.trapecio_info {
  z-index: 20;
  display: grid;
  position: absolute;
  align-items: center;
  left: 1vw;
  width: auto;
  height:65%;
  border-bottom: 2vw solid #222;
  border-right: 5vw solid transparent;
  object-fit: cover;
  transform: scaleX(-1);
  

}
.trapecio_info div{
  display: inline-flex;
background-color: grey;
position: space-betewn;
}


 .trapecio_info svg{
width: 1.2rem;
height: 1.2rem;
fill:white;

}

 .trapecio_info .spamer span {
display: none;
}

 .trapecio_info .spamer:hover span {
  color: yellow;
 background: rgba(76, 175, 80, 0.1);
 font-size: 1em;
  display: block;
}


