.usos {
  z-index: 999;
  position: absolute;
  bottom: -0.3rem;
  right: -0.1rem;
  /* Otros estilos opcionales para el párrafo */
}

.ptos_implementos {
  z-index: 999;
  color: #FF7F00; /* Color alegre que combina con #854D0D */
  font-family: "Comic Sans MS", cursive; /* Fuente provocativa */
  font-size: 24px; /* Tamaño de letra personalizado */
  text-transform: uppercase; /* Convertir texto en mayúsculas */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* Sombra de texto para un efecto llamativo */
  position: relative; /* Establecer posición relativa */
  padding: 0 20px; /* Añadir espacio para el círculo */
  top: -1rem;
  left: -1rem;
}

.ptos_implemento {
  z-index: 999;
  color: #FF7F00; /* Color alegre que combina con #854D0D */
  font-family: "Comic Sans MS", cursive; /* Fuente provocativa */
  font-size: 24px; /* Tamaño de letra personalizado */
  text-transform: uppercase; /* Convertir texto en mayúsculas */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* Sombra de texto para un efecto llamativo */
  position: relative; /* Establecer posición relativa */
  display: block; /* Colocar el span en una nueva línea */


}
.ptos_implementos span.ptos_implemento {
  display: flow;
    padding-top: 0.5em;
  color: #FF7F00; /* Color alegre que combina con #854D0D */
  font-family: "Comic Sans MS", cursive; /* Fuente provocativa */
  font-size: 16px; /* Tamaño de letra personalizado para el span */
  text-transform: none; /* Desactivar la transformación de texto en mayúsculas */
  text-shadow: none; /* Eliminar la sombra de texto en el span */
}








